/* ====== Theme tokens ====== */
:root{
  --brand-primary:  #FF4E00; /* orange */
  --brand-tertiary: #00B2FF; /* cyan */
  --brand-deep:     #183059; /* deep blue */
  --brand-gray:     #B3B6B7;

  --text: #000;
  --muted:#666;

  --brand-gap: 1rem;
  --logo-size: 84px;   /* logomark box height */
  --logo-grow: 1.13;   /* 13% larger than name visually */
  --name-nudge: 6px;   /* tiny baseline nudge on hover */
  --tile-size: 64px;   /* social tile square */
  --icon-size: 36px;   /* PNG icon inside tile */
  --tile-radius: 18px; /* iOS-like rounded shape */
  --ease: cubic-bezier(.22,.61,.36,1);
  --ease-inout: cubic-bezier(.4,0,.2,1);
}

* { box-sizing: border-box; }
html, body { height: 100%; }
body {
  margin: 0;
  color: var(--text);
  font-family: "Helvetica Neue", Helvetica, Arial, system-ui, -apple-system, sans-serif;
}

/* Remove bullets everywhere we own lists */
ul { list-style: none; padding: 0; margin: 0; }

/* ====== Page grid: center brand; pin social to bottom ====== */
.site{
  min-height: 100svh;
  display: grid;
  grid-template-rows: 1fr auto; /* top fills, bottom hugs content */
}

/* Row 1: perfectly center the brand block both ways */
.brand-wrap{
  display: grid;
  place-items: center;
  padding: 2rem;
}

/* ====== Brand block ====== */
.brand{
  display: grid;
  justify-items: center;
  gap: .75rem;
  text-align: center;
}

/* One row: logomark + logotype aligned visually at the bottom */
.brand-row{
  display: inline-flex;
  align-items: flex-end; /* baseline-like */
  gap: var(--brand-gap);
}

/* Logomark wrapper: fixed box; images stacked absolutely */
.brand-logo{
  position: relative;
  width: calc(var(--logo-size) * var(--logo-grow));
  height: var(--logo-size);
  display: inline-block;
}

/* Two stacked images (gray & color) with smooth crossfade */
.brand-logo__img{
  position: absolute;
  inset: 0;
  width: 100%;
  height: 100%;
  object-fit: contain;
  transition:
    opacity   380ms var(--ease-inout),
    transform 420ms var(--ease-inout);
  will-change: opacity, transform;
  backface-visibility: hidden;
}
.brand-logo__img.img--gray  { opacity: 1; transform: scale(1); }
.brand-logo__img.img--color { opacity: 0; transform: scale(.985); }

/* Hover the whole brand to reveal color smoothly */
.brand:hover .brand-logo__img.img--gray  { opacity: 0; transform: scale(1.03); }
.brand:hover .brand-logo__img.img--color { opacity: 1; transform: scale(1);    }

/* Logotype: single image; only a slight vertical nudge on hover */
.brand-name{
  height: calc(var(--logo-size) * .68);
  width: auto;
  display: block;
  transform: translateY(0);
  transition: transform 260ms var(--ease);
  will-change: transform;
}
.brand:hover .brand-name{
  transform: translateY(calc(-1 * var(--name-nudge)));
}

/* Tagline */
.tagline{
  font-weight: 400;
  line-height: 1.25;
  color: var(--muted);
  margin: .25rem 0 0;
  font-size: clamp(1.1rem, 2.2vw, 1.35rem);
}

.t-word{
  font-weight: 700;
  transition: color 260ms var(--ease-inout);
}

/* NEW: when hovering the brand (logo OR name), color both words */
.brand:hover .t-word.t-shift  { color: var(--brand-primary);  }
.brand:hover .t-word.t-design { color: var(--brand-tertiary); }

/* Optionally color both words when hovering the whole brand:
.brand:hover .t-word.t-shift  { color: var(--brand-primary); }
.brand:hover .t-word.t-design { color: var(--brand-tertiary); }
*/

/* ====== Social row (pinned to bottom row of grid) ====== */
.social{
  padding: 1rem 1.25rem calc(env(safe-area-inset-bottom, 0) + 1rem);
  display: flex;
  justify-content: center;
}
.social-grid{
  display: flex;
  gap: 16px;
  flex-wrap: wrap;      /* wrap if narrow */
  justify-content: center;
  align-items: center;
}

/* Uniform tile for every network */
.tile{
  position: relative;
  width: var(--tile-size);
  height: var(--tile-size);
  display: inline-grid;
  place-items: center;
  text-decoration: none;
  transform: translateZ(0);
}

/* Rounded app-like background; appears on hover */
.pill{
  position: absolute;
  inset: 0;
  border-radius: var(--tile-radius);
  background: transparent;
  transform: scale(.92);
  opacity: 0;
  transition:
    background-color 220ms var(--ease-inout),
    opacity          220ms var(--ease-inout),
    transform        220ms var(--ease-inout);
}

/* Two stacked PNGs (black default, white hover) */
.icon-wrap{
  position: relative;
  width: var(--icon-size);
  height: var(--icon-size);
}
.icon{
  position: absolute;
  inset: 0;
  width: 100%;
  height: 100%;
  object-fit: contain;
  transition: opacity 220ms var(--ease-inout), transform 220ms var(--ease-inout);
  will-change: opacity, transform;
  backface-visibility: hidden;
}
.img--default { opacity: 1; transform: scale(1);    }
.img--hover   { opacity: 0; transform: scale(.96);  }

/* Hover: show pill, swap to white icon, subtle scale */
.tile:hover .pill{
  opacity: 1;
  transform: scale(1);
}
.tile:hover .img--default{ opacity: 0; transform: scale(1.04); }
.tile:hover .img--hover  { opacity: 1; transform: scale(1.02); }

/* Kill any stray bullets in the social list */
.social li { list-style: none; }

/* Brand colors per network (tile background on hover) */
.social-item.github   .pill { background: #000000; }
.social-item.medium   .pill { background: #000000; }
.social-item.px500    .pill { background: #000000; }
.social-item.bluesky  .pill { background: #1185FE; }
.social-item.mastodon .pill { background: #3088D4; }
.social-item.email    .pill { background: #157CFF; } /* iOS Mail-ish */

/* ====== Responsive ====== */
@media (max-width: 520px){
  :root{
    --logo-size: 72px;
    --tile-size: 56px;
    --icon-size: 32px;
  }
  .brand { gap: .5rem; }
}